@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600|Open+Sans:400,400i,600);
/*Change this to eliminate all the unused Montserrat thicknesses*/
.bigbutton{
	background-color: hsl(195, 100%, 35%);
	border: none;
	padding: 10px;
	border-radius: 15px;
	min-width: 200px;
	color: white;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
.bigbutton:hover{
	background-color: hsl(195, 100%, 30%);
}
.bigbutton:focus{
	outline: none;
}
.bigbutton:active{
	background-color: hsl(195, 100%, 25%);
	-webkit-transition: none;
	transition: none;
}
body{
	font-family: Open Sans, Montserrat, sans serif;
	--title-font: Montserrat, Open Sans, sans serif;
	--body-font: Open Sans, Montserrat, sans serif;
}
#closebtn{
	position: fixed;
	top: 10px;
	right: 20px;
	width: 50px;
	height: 50px;
	border-radius: 35.4px;
	border: 1px solid hsl(0, 0%, 70%);
}
#close{
	position: fixed;
	top: 20px;
	right: 30px;
	width: 30px;
	height: 30px;
}
#closewrap{
	position: fixed;
	top: 9px;
	right: 19px;
	width: 52px;
	height: 52px;
	border-radius: 35.4px;
	padding: 0px;
	display: none;
	z-index: 2500;
}
#closewrap:hover{
	cursor: pointer;
}
#fullnavcont{
	background-color: hsl(0, 0%, 90%);
	padding: 30px;
	border-radius: 10px;
}
.fullnavlink{
	font-size: 1.5em;
	color: hsl(195, 100%, 40%);
	-webkit-transition: color 0.2s ease-out;
	transition: color 0.2s ease-out;
}
#fullnav{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: hsla(0, 0%, 90%, 0.8);
	text-align: center;
	display: none;
	z-index: 2000;
}
.gray{
	color: hsl(0, 0%, 50%);
}
.navbar{
	height: 80px;
	border-bottom: 1px solid hsl(0, 0%, 75%);
	text-align: right;
	margin-bottom: 80px;
	position: relative;
}
.navcont{
	display: inline-block;
	position: absolute;
	bottom: 16px;
}
#navdrop{
	display: none;
	position: absolute;
	bottom: 8px;
	right: -20px;
}
.navright{right: 5%}
.navleft{left: 5%}
.navlink{
	color: hsl(195, 20%, 15%);
	font-family: Montserrat;
	font-size: 20px;
	margin-left: 70px;
	margin-right: 10px;
	display: inline-block;
	font-weight: 500;
}
.navlink:hover{
	color: hsl(195, 100%, 40%);
	text-decoration: none;
}

.mainlogonav{ 
	font-family: Montserrat;
	font-weight: 600;
	font-size: 48px;
	line-height: 48px;
	background: hsl(195, 100%, 50%);
	background: -webkit-linear-gradient(hsl(195, 100%, 55%) 0%, hsl(195, 100%, 45%) 60%, hsl(195, 100%, 44.5%) 61%, hsl(195, 100%, 35%));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	display: inline-block;
	vertical-align: bottom;
	margin-bottom: -3px;
}
.mainlogonav:hover, .mainlogofoot:hover{
	background: hsl(195, 100%, 45%);
	background: -webkit-linear-gradient(hsl(195, 100%, 50%) 0%, hsl(195, 100%, 40%) 60%, hsl(195, 100%, 39.5%) 61%, hsl(195, 100%, 35%));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.shrink{
	font-size: 0.7em;
}
.title{
	text-align: center;
	font-family: var(--title-font);
	font-size: 4em;
	text-transform: uppercase;
	letter-spacing: 0.1em;
	margin-bottom: 60px;
}
.triangledown{
	width: 0;
	height: 0;
	display: inline-block;
	border-left: 8px solid transparent;
	border-right: 8px solid transparent;
	border-top: 16px solid hsl(195, 40%, 40%);
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
.triangledown:hover{
	border-top-color: hsl(195, 100%, 40%);
	cursor: pointer;
}


.outer {
	display: table;
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
}
.inner {
	display: table-cell;
	vertical-align: middle;
}

@media only screen and (max-width: 991.98px){
	.navlink{
		font-size: 18px;
		margin-left: 40px;
	}
	.mainlogonav{
		font-size: 40px;
		line-height: 40px;
		margin-bottom: -2px;
	}
	.navleft{
		left: 2%;
	}
	.navright{
		right: 2%;
	}
	
}

@media only screen and (max-width: 767.98px){
	#navdrop{
		display: inline-block;
	}
	.navright{
		display: none;
	}
	.navleft{
		left: 5%;
	}
	.title{
		font-size: 2.8em;
	}
	.mainlogonav{
		font-size: 48px;
		line-height: 48px;
	}
}
.clubbutton{
	display: inline-block;
	line-height: 40px;
	padding: 0;
	height: 40px;
	width: 150px;
	border-radius: 29px;
	border: 1px solid hsl(0, 0%, 60%);
	color: inherit;
}
.clubbutton:hover{
	border-color: hsl(0, 0%, 50%);
	color: inherit;
	text-decoration: inherit;
}
.clubbutton:focus{
	outline: none;
}
.clubtitle{
	font-size: 3em;
	text-align: center;
}
.clubwrapper{
	padding: 0 50px;
}
.edit{
	background-color: hsl(0, 0%, 95%);
	margin-left: 2px;
}
.info{
	/*background-color: hsl(0, 0%, 95%);*/
	border: 1px solid hsl(0, 0%, 25%);
	border-radius: 10px;
	padding: 20px 0;
}
.info a{
	word-wrap: break-all;
}
.middle{
	background-color: hsl(0, 0%, 95%);
	border-width: 1px 0;
	border-color: hsl(0, 0%, 75%);
	border-style: solid;
	padding: 20px;
}
.nomargin{
	margin: 0;
}
.username{
	color: hsl(0, 0%, 60%);
	text-align: center;
}
.sidead{
	border: 1px solid black;
	flex: 0 0 301px;
	height: 600px;
	overflow: hidden;
}
.subscribe{
	background-color: hsl(195, 100%, 70%);
}
.subscribe:disabled{
	background-color: hsl(0, 0%, 85%);
}
.subbed{
	background-color: hsl(0, 0%, 75%);
}
.subbed:hover{
	border-color: hsl(0, 100%, 50%);
	color: hsl(0, 100%, 50%);
}
.ql-image{
	max-width: 100% !important;
}
.quillcontent{
	padding: 0 20px;
	margin-top: 20px;
}
.title{
	margin-bottom: 15px;
}
.ql-size-small{font-size: 0.8em}
.ql-size-large{font-size: 1.5em}
.ql-size-huge{font-size: 2em}
.fullsearchwrap{
	margin-bottom: 5px;
}
.fullresult{
	background-color: hsl(0, 0%, 95%);
	margin-bottom: 15px;
	/*height: calc(600px - 20vw);*/
	border: 1px solid transparent;
	border-radius: 5px;
	-webkit-transition: border-color 0.2s ease;
	transition: border-color 0.2s ease;
	overflow: hidden;
}
.fullresult:hover{
	border-color: hsl(0, 0%, 80%);
}
.resultbody{
	padding: 10px 15px;
	opacity: 0.9;
}
.resultlink{
	color: inherit;
}
.resultlink:hover{
	text-decoration: inherit;
	color: inherit;
}
.resulthead{
	padding: 20px 15px;
}
.resulttitle{
	font-family: var(--body-font);
	margin-bottom: 0;
}
.searchbar{
	max-width: 80%;
	width: 600px;
	margin: auto;
}
.searchbarwrap{
	padding: 20px;
	background-color: hsl(200, 100%, 90%);
}
#searchbutton{
	width: calc(25% - 3px);
	min-width: 0;
	min-width: initial;
	border-radius: 0;
	border-radius: 0 10px 10px 0;
	vertical-align: top;
	border: 1px solid hsl(0, 0%, 75%);
	height: 46px;
}
#searchtext{
	width: 75%;
	height: 46px;
	border: none;
	border-radius: 10px 0 0 10px;
	padding: 3px 8px;
	vertical-align: top;
	border: 1px solid hsl(0, 0%, 75%);
	border-right: none;
}
#searchtext:focus{outline: none}
.sidead{
	border: 1px solid black;
	flex: 0 0 301px;
	height: 600px;
	overflow: hidden;
}
.sidead img, .topad img{
	max-width: 100%;
	max-height: 100%;
}
.taglist{
	color: hsl(0, 0%, 50%);
	margin-bottom: 0;
}
.topad{
	border: 1px solid black;
	width: 302px;
	height: 602px;
	overflow: hidden;
}
.topadcontainer{
	text-align: center;
}

@media screen and (max-width: 768px){
	body{
		font-size: 0.8em;
	}
	h1{
		font-size: 2.5em;
	}
	h4{
		font-size: 1.5em;
	}
}

@media screen and (max-width: 650px){
	.topad{
		width: 202px;
		height: 402px;
	}
}

@media screen and (max-width: 450px){
	.topad{
		width: 152px;
		height: 302px;
	}
	#searchbutton{
		width: 100px;
	}
	#searchtext{
		width: calc(100% - 103px);
	}
}

.interestcontainer{
	margin: auto;
}
.interest{
	background-color: hsl(0, 0%, 100%);
	margin: 1px;
	min-width: 100px;
	padding: 0 5px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	border: 1px solid transparent;
	-webkit-transition: background-color 0.1s ease, border-color 0.3s ease;
	transition: background-color 0.1s ease, border-color 0.3s ease;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	display: inline-block;
}
.interest:hover{
	border-color: hsl(0, 0%, 75%);
	cursor: pointer;
}
.selected{
	background-color: hsl(0, 0%, 75%);
}
.profilesection{
	background-color: hsl(0, 0%, 95%);
	border-radius: 10px;
	padding: 20px;
	border: 1px solid hsl(0, 0%, 75%);
	-webkit-transition: border-color 0.3s ease;
	transition: border-color 0.3s ease;
	color: inherit;
	margin: 10px;
}
.prosectionheader{
	text-align: center;
	font-family: var(--body-font);
	color: black;
}
.savebutton:disabled{
	background-color: hsl(0, 0%, 75%);
	color: hsl(0, 0%, 90%);
}
